import React from 'react'
import {
  Typography,
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia
} from '@material-ui/core'

import useStyle from './style'

const CartItem = ({ item,HandleUpdateQty,HandleRemoveItem }) => {
  const classes = useStyle();

  return (
    <Card>
      <CardMedia image={item.image.url} alt={item.name} className={classes.media} />
      <CardContent className={classes.cardContent}>
        <Typography variant="h4">{item.name}</Typography>
        <Typography variant="h5">{item.price.formatted_with_symbol}</Typography>
      </CardContent>
      <CardActions className={classes.CardActions}>
        <div className={classes.buttons}>
          <Button type="button" size="small" onClick={() => {
            HandleUpdateQty(item.id, item.quantity - 1)
          }}>-</Button>
          <Typography>{item.quantity}</Typography>
          <Button type="button" size="small" onClick={() => {
            HandleUpdateQty(item.id, item.quantity + 1)
          }}>+</Button>
        </div>
        <Button variant="contained" color="secondary" type="button" onClick={() => HandleRemoveItem(item.id)}>Remove</Button>
      </CardActions>
    </Card>
  )
}

export default CartItem
